<template>
  <div>
      <h1>GardeDetail</h1>
        <div class="card">
            <div>
                <label>id:</label>
            </div>  
            <div>
                <label>Pharmacie:</label>
            </div>                      
            <div>
                <h6>Nombre de gardes réelles du mois:</h6>
                <h4>{{gardeNombre()}}</h4>
            </div>
            <div v-for="(garde,index) of gardes" v-bind:key="index">
                <h4>{{garde.nom}}</h4>
               <label>Du:<input type="date" v-model="garde.debut"> </label>
               <label>Au:<input type="date" v-model="garde.fin"> </label>
            </div>

            <b-button pill variant="success" @click="gardeSave(gardes)">Save</b-button> 
            <b-button pill variant="dark" @click="gardeCancel()">Cancel</b-button>
     </div>
  </div>
</template>

<script>
export default {
    name:'GardeDetail',

    data(){
        return {
            gardes: this.$store.getters.getDetailGarde
        }
    },

    methods: {
        gardeSave(gardes){
            this.$store.commit('setDetailGarde',gardes);
            this.gardeCancel();
        },
        gardeCancel(){
            this.$router.push("/pharmacie-detail/");
        },
        gardeNombre(){
            return 5;
        }
    }
}
</script>

